import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { AlertController, ToastController } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { Router, ActivatedRoute } from '@angular/router';
import { OptionService } from '../services/option.service';
import { Option } from '../models/option';
import { Kind } from '../models/kind';
import { KindService} from '../services/kind.service';
import { ServiceStatus } from '../models/service-status.enum';

@Component({
  selector: 'app-options',
  templateUrl: './options.page.html',
  styleUrls: ['./options.page.scss'],
})
export class OptionsPage implements OnInit, OnDestroy {

  @Input() Route$: Subscription;
  @Input() Kind: Kind;
  @Input() Options: Array<Option>;

  constructor(
    private service: OptionService,
    private router: Router,
    private route: ActivatedRoute,
    private ac: AlertController,
    private tc: ToastController,
    private ks: KindService) { }

  ngOnInit() {

  }

  ngOnDestroy() {
    if (this.Route$) {
      this.Route$.unsubscribe();
    }
  }

  private GetKind(kind_id: string): Promise<Kind> {
    const self = this;
    return new Promise((resolve, reject) => {
      this.ks.asyncGet(kind_id).subscribe({
        next(ris) {
          resolve(ris);
        },
        error(err) {
          resolve(null);
        }
      });
    });
  }

  private GetOptions(kind_id: string): Promise<Array<Option>> {
    const self = this;
    return new Promise((resolve, reject) => {
      self.service.asyncGetList(kind_id).subscribe({
        next(ris) {
          resolve(ris);
        },
        error(err) {
          resolve([]);
        }
      });
    });
  }

  private GetKindId(): Promise<string> {
    const self = this;
    return new Promise((resolve, reject) => {
      self.Route$ = this.route.params.subscribe(params => {
        resolve(params['kind_id']);
      });
    });
  }

  private DeleteItem(id: string): Promise<ServiceStatus> {
    const self = this;
    return new Promise((resolve, reject) => {
      self.service.asyncDelete(self.Kind._id, id).subscribe({
        next(ris) {
          resolve(ris);
        },
        error(err) {
          resolve(ServiceStatus.FAILED);
        }
      });
    });
  }

  private async ConfirmDelete(id: string) {
    const self = this;
    const alert = await this.ac.create({
      header: 'Cancellazione opzione',
      message: 'Stai per <strong>cancellare</strong> una opzione',
      buttons: [
        {
          text: 'Annulla',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (cnl) => {}
        },
        {
          text: 'Conferma',
          handler: async () => {
            const ss = await this.DeleteItem(id);
            if (ss === ServiceStatus.OK) {
              await self.LoadData();
              await self.ShowToast('Opzione correttamente cancellata.');
            } else {
              await self.ShowToast('Si Ã¨ verificato un problema. Riprova!');
            }
          }
        }
      ]
    });
    await alert.present();
  }

  private async ShowToast(message: string) {
    const toast = await this.tc.create({
      message,
      duration: 2000
    });
    await toast.present();
  }

  private async LoadData() {
    const kindId = await this.GetKindId();
    this.Options = await this.GetOptions(kindId);
    this.Kind = await this.GetKind(kindId);
  }

  async ionViewWillEnter() {
    await this.LoadData();
    localStorage.setItem('kindName', this.Kind.name);
  }

  onCreateClick() {
    this.router.navigate(['/option-form', this.Kind._id, 'new']);
  }

  onItemClick(id: string) {
    this.router.navigate(['/option-form', this.Kind._id, id]);
  }

  async onItemDelete(id: string) {
    await this.ConfirmDelete(id);
  }

  onBackClick() {
    this.router.navigate(['/home']);
  }
}
